"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { UmkmShell } from "@/components/umkm-shell"
import { getStorageItems } from "@/lib/storage"
import type { Kuliner } from "@/lib/types"
import { UmkmDashboard } from "./dashboard"

export default function UmkmPage() {
  const [items, setItems] = useState<Kuliner[]>([])
  const [mounted, setMounted] = useState(false)
  const router = useRouter()

  useEffect(() => {
    // 1. Tandai bahwa komponen sudah terpasang (mounted) di browser
    setMounted(true)

    // 2. Ambil data kuliner dari storage (sementara masih localStorage)
    const stored = getStorageItems()
    setItems(stored)

    // 3. Logika Proteksi Role UMKM (Simulasi)
    // Untuk sekarang gua matikan dulu supaya lo bisa liat dashboardnya tanpa login.
    // Nanti saat Laravel sudah siap, kita pakai token asli.
    /*
    const isUmkmLoggedIn = localStorage.getItem("umkm_token")
    if (!isUmkmLoggedIn) {
      router.push("/umkm/auth")
    }
    */
  }, [router])

  // Cegah error hydration: Jangan render apapun di server sebelum masuk ke client
  if (!mounted) {
    return (
      <div className="flex h-screen items-center justify-center bg-[#f4e8d1]">
        <div className="text-center">
          <div className="mb-4 h-12 w-12 animate-spin rounded-full border-4 border-[#4e5b31] border-t-transparent mx-auto"></div>
          <p className="text-[#3b2f2f] font-medium italic">Memasuki Portal Mitra...</p>
        </div>
      </div>
    )
  }

  return (
    <UmkmShell title="Dashboard Utama Mitra UMKM" showAddButton>
      <div className="max-w-7xl mx-auto">
        {/* Info Box Khusus UMKM */}
        <div className="mx-6 mt-6 p-4 bg-[#4e5b31]/10 border border-[#4e5b31]/20 rounded-2xl flex items-center gap-4">
          <div className="bg-[#4e5b31] text-white p-2 rounded-lg">
            üè†
          </div>
          <div>
            <h4 className="font-bold text-[#3b2f2f] text-sm">Selamat Datang kembali, Mitra!</h4>
            <p className="text-[#6e5849] text-xs">Kelola menu, update lokasi, dan pantau ulasan pelanggan Anda di sini.</p>
          </div>
        </div>

        {/* Komponen Dashboard yang berisi Table/Grid daftar kuliner milik si UMKM */}
        <UmkmDashboard initialItems={items} onItemsChange={setItems} />
      </div>
    </UmkmShell>
  )
}